/*===== HEADER =====*/
.navbar-1 {
  display: flex;
  justify-content: center;
  align-items: center;
  max-width: 1200px;
  margin: 0 auto;
  width: 100%;
}



/*===== Image =====*/
.image-text {
  position: absolute;
  display: flex;
  flex-direction: column;
  text-align: center;
  color: white;
  padding: 10px;
  text-shadow: 0 2px 4px rgba(0, 0, 0, 0.5);
  transition: transform 0.3s ease, color 0.3s ease;
}

.image-text h2 {
  margin: 0;
  font-size: 1.8em;
  font-weight: bold;
}
.image-text p {
  margin: 5px 0 0;
  font-size: 1em;
}

.main-left_1 {
  width: 100%;
  position: relative;
  padding: 20px;
  background-color: #ffffff;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  max-width: 400px;
  margin: 0 auto;
  z-index: 1;
  transition: transform 0.3s ease, box-shadow 0.3s ease, background 0.3s ease;
}
.main-left_1::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: url('./../../../public/assets/img/background/background-1.webp');
  background-size: cover;
  background-position: center;
  border-radius: 8px;
  opacity: 0.6;
  z-index: -1;
}
.main-left_1 h1 {
  font-size: 36px;
}
.main-left_1 p {
  font-size: 18px;
  margin-top: 30px;
  line-height: 1.5;
}

/*===== HOVER-Effect =====*/
.main-left_1:hover {
  transform: translateY(-10px);
  box-shadow: 0 0 20px rgba(0, 133, 89, 0.7), 0 0 40px rgba(0, 126, 148, 0.5), 0 0 60px rgba(0, 115, 229, 0.3);
  background: linear-gradient(123deg, #008559 0%, #007e94 50%, #00e59c 100%);
  background-clip: border-box;
  transition: transform 0.3s ease, box-shadow 0.3s ease, background 0.3s ease;
}
.main-left_1:hover .info-content {
  opacity: 0;
  transform: translateY(-100%);
  transition: opacity 0.3s ease, transform 0.3s ease;
}
.main-left_1:hover .info-image {
  opacity: 1;
  transform: translateY(0);
  transition: opacity 0.3s ease, transform 0.3s ease;
}


/* Right */
.main-right_1 {
  max-width: 45%;
  position: relative;
}
.main-right_1 img {
  width: 100%;
  height: auto;
  border-radius: 30px;
  transition: transform 0.3s ease, filter 0.3s ease;
}
.main-right_1 img:hover {
  transform: scale(1.05); 
  filter: brightness(0.9);
}

@media (max-width: 768px) {
  .matrix_up {
    flex-direction: column;
  }
}