/*===== Сброс стилей =====*/
html {
  scroll-behavior: smooth;
}
body {
  font-family: 'Gilroy', sans-serif;
  margin: 0;
  padding: 0;
  background: linear-gradient(5deg, rgba(212, 252, 121, 0.4) 0%, rgba(150, 230, 161, 0.4) 50%, rgba(0, 198, 255, 0.4) 100%);
  background-attachment: fixed;
  color: #333;
  overflow-y: auto;
}


.container {
  display: flex;
  align-items: center;
  padding: 40px;
  max-width: 1200px;
  margin: 0 auto;
}

.blurred-background {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(255, 255, 255, 0.7); 
  filter: blur(10px);
  z-index: 1;
}

main {
  padding: 20px;
}
.image-container {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
}
.overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.426);
  border-radius: 30px;
  transition: background-color 0.3s ease;
}


/*=== Flex ===*/
.flex-container {
  display: flex;
  align-items: center;
  text-align: center;
  justify-content: space-evenly;
}  
.flex-container .section-title {
   margin-right: 10px;
}



/*===== TABLE =====*/
table {
  border-collapse: collapse;
  border: none;
}
table td, table th {
  border: none;
}

.table-container {
  display: flex;
  justify-content: center;
}  
.table {
  margin: 0 auto;
  width: 100%;
  border-collapse: collapse;
}
.table td {
  padding: 15px;
  vertical-align: top;
}

.inner-table {
  width: 100%;
  border-spacing: 10px;
}
.inner-table td {
  padding: 5px;
  background-color: #f9f9f9;
  border-radius: 5px;
}

.tabl {
  width: 100%;
  
  border-collapse: collapse;
  border-radius: 10px;

  line-height: 1.5rem;
}

.header-row {
  font-size: 14px;
  font-weight: bold;
  color: #000000;
}
.header-row td {
  padding: 10px;
  background: rgba(224, 224, 224, 0.5);
  text-align: center;
  vertical-align: middle;
}

/* Стили для соединительной стрелки */
.icon-container {
  display: flex;
  align-items: center;
  justify-content: center;
}

.chakra-row td {
  text-align: center;
  vertical-align: middle;
  color: black;
  background: rgba(224, 224, 224, 0.5);
}
.chakra-number {
  padding: 10px;
}

.total-row {
  font-weight: bold;
}
.total-row td {
  padding: 10px;
  text-align: center;
  vertical-align: middle;
  color: black;
}
.total-row, .n54, .n55, .n56 {
  background: rgba(224, 224, 224, 0.5);
}



/*===== BUTTONS =====*/
button {
  font-family: 'Gilroy', sans-serif;
  color: white;
  border: none;
  padding: 10px 20px;
  cursor: pointer;
}

/*=== Стиль кнопки ===*/
.btn {
  padding: 12px 20px;
  background-color: #009688;
  margin-top: 10px;
  border-radius: 4px;
  color: #fff;
  font-size: 14px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}
.btn:hover {
  background-color: #89c194;
}



/*=== Скрытие элементов ===*/
.hidden {
  display: none;
}


/* Стили для контейнера SVG */
.svg-container {
  margin-top: 20px;
  text-align: center;
}
svg {
  max-width: 100%;
  height: auto;
}
