/*===== MAIN =====*/
/*===== Поля ввода данных =====*/
.main-left {
    flex: 1;
}
.main-left h1 {
    font-size: 36px;
}
.main-left p {
    font-size: 18px;
    margin-top: 30px;
    line-height: 1.5;
}


/* Right */
.main-right {
    max-width: 45%;
}
.main-right img {
    width: 100%;
    height: auto;
    border-radius: 30px;
    transition: transform 0.3s ease, filter 0.3s ease;
}
.main-right img:hover {
    transform: scale(1.05); 
    filter: brightness(0.9);
}


/*===== MAIN (Изображения) =====*/
.image-container {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
}
.image-container:hover img {
    transform: scale(1.05);
    filter: brightness(0.9);
}
.image-container:hover .overlay {
    background-color: rgba(0, 0, 0, 0.5);
}
/* Стиль для текста на изображении */
.image-text {
    position: absolute;
    display: flex;
    flex-direction: column;
    text-align: center;
    color: white;
    padding: 10px;
    text-shadow: 0 2px 4px rgba(0, 0, 0, 0.5);
    transition: transform 0.3s ease, color 0.3s ease;
}
.image-container:hover .image-text {
    transform: scale(1.05);
    color: #f8f9fa;
}

.image-text h2 {
    margin: 0;
    font-size: 1.8em;
    font-weight: bold;
}
.image-text p {
    margin: 5px 0 0;
    font-size: 1em;
}

/*===== MAIN.css =====*/
@media (max-width: 768px) {
    .container.main_1 {
        padding: 10px;
    }
    .main-left, .main-right {
        width: 90%;
        padding: 0;
    }

    /* Left */
    .main-left {
        flex: 1;
        display: flex;
        flex-direction: column;
        text-align: center;

        padding: 0;
    }
    /* Left (text) */
    .main-left h1 {
        font-size: 25px;
        margin-bottom: 15px;
    }
    .main-left_1 p {
        font-size: clamp(12px, 2.5vw, 16px);
        margin-bottom: clamp(10px, 3vw, 20px);
    }
    .main-left p {
        font-size: 16px;
        margin-bottom: 20px;
    }

    .main-right {
        display: none;
    }

    .tabs {
        flex-direction: column;
    }

    .tab {
        width: 100%;
        margin-bottom: 10px;
    }
}